import request from '@/apis/request.wms'

// 列表
export function getListApi(data) {
  return request({
    url: '/basic/wmsWarehouseStorageLocation/pageList',
    method: 'post',
    data
  })
}
// 下拉列表
export function getPullListItemApi(data) {
  return request({
    url: '/basic/wmsWarehouseStorageLocation/listItem',
    method: 'post',
    data
  })
}
// 新增
export function getAddApi(data) {
  return request({
    url: '/basic/wmsWarehouseStorageLocation/add',
    method: 'post',
    data
  })
}
// 修改
export function getEditApi(data) {
  return request({
    url: '/basic/wmsWarehouseStorageLocation/edit',
    method: 'post',
    data
  })
}
// 删除
export function getDeleteApi(data) {
  return request({
    url: '/basic/wmsWarehouseStorageLocation/delete',
    method: 'post',
    data
  })
}
// 懒加载查询
export function getLazySearchApi(data) {
  return request({
    url: '/basic/wmsWarehouseStorageLocation/childrenList',
    method: 'post',
    data
  })
}

// 库位树列表
export function getTreeListApi(data = {}) {
  return request({
    url: '/basic/wmsWarehouseStorageLocation/treeList',
    method: 'post',
    data,
    timeout: 5 * 60 * 1000
  })
}

// 点击树查询右侧详情
export function getTreeNodeDetailApi(data) {
  return request({
    url: '/basic/wmsWarehouseStorageLocation/detail',
    method: 'post',
    data
  })
}

// 查询权限内仓库列表
export function getWarehouseListApi(data = {}) {
  return request({
    url: '/basic/wmsWarehouseSap/listAllNameAndId',
    method: 'post',
    data
  })
}

