<template>
  <el-dialog
    :title="title"
    top="15vh"
    :visible="value"
    :show-close="true"
    width="750px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar class="scroll-dialog">
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-column-sort="false"
        :column-list="columnList"
        :table-data="tableData"
        :show-operate="false"
        @refresh="searchInit"
      />
    </el-scrollbar>
  </el-dialog>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getReservationDetailsListApi } from '@/apis/modules/warehouse/inventory'

export default {
  components: {
  },
  mixins: [paginationPage],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    },
    title: {
      type: [String, Number],
      default: '所属预留单信息'
    }
  },
  data() {
    return {
      loading: false,
      columnList: [
        {
          module: 'word',
          prop: 'reservedNumber',
          label: '预留单',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'subNumber',
          label: '预留子单',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialNum',
          label: '物料数量',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      tableData: []
    }
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getReservationDetailsListApi({ materialId: this.extraInfo.materialId }).then(res => {
          const { code, data } = res
          if (!code) {
            this.tableData = data
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 弹窗关闭前操作
    beforeClose(done) {
      typeof done === 'function' && done()
      this.$emit('input', false)
    },
    // 弹窗打开初始化
    initSelect() {
      this.init()
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
    height: 440px;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
}
::v-deep .el-dialog__body {
    padding-bottom: 0;
}
</style>

