<template>
  <el-drawer
    :title="title"
    :visible="value"
    append-to-body
    destroy-on-close
    :before-close="beforeClose"
    :close-on-press-escape="false"
    :direction="direction"
    :size="size"
    :wrapper-closable="false"
    custom-class="user-drawer"
  >
    <div v-loading="pageLoading">
      <el-form ref="form" :model="query" inline>
        <el-form-item prop="warehouseId" label="仓库：">
          <el-select v-model="query.warehouseId" filterable clearable placeholder="仓库名称" style="width: 178px;" @change="selectWarehouse">
            <el-option
              v-for="(item,index) in warehousePullList"
              :key="index"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
      </el-form>
      <el-scrollbar class="scroll-drawer">
        <div class="draw-container">
          <!-- Tip 区 -->
          <el-tabs v-if="districtData.length" v-model="activeName" type="card" @tab-click="handleTabClick">
            <el-tab-pane
              v-for="(quItem, index) in districtData"
              :key="index"
              :name="`${quItem.warehouseId}-${quItem.id}`"
            >
              <!-- 选择库位页面的区字体调大颜色不变，SAP库字体调小改为浅灰色 -->
              <span slot="label">
                <span style="font-size: 18px;font-weight: bold;">{{ `${quItem.name}${quItem.level_dictText}` }}</span>
                <span style="color: #909399;">{{ quItem.sapName | sapKuStr }}</span>
              </span>
              <div v-if="subzoneData.length" class="huojia-sty">
                <!-- Tip 货架 -->
                <div v-for="(hjItem, i) in subzoneData" :key="i">
                  <el-card :body-style="{ minHeight: '325px', width: getHjWidth(hjItem.children)}">
                    <div slot="header" class="clearfix">
                      <div style="text-align: center;cursor: pointer;" @click="chooseHuoJia(hjItem)">{{ hjItem.name }}架</div>
                    </div>
                    <template v-if="hjItem.children.length">
                      <!-- Tip 层 -->
                      <template v-for="(cengItem, f) in hjItem.children">
                        <div :key="f" class="tier">
                          <div class="floor-name-sty" @click="chooseCeng(cengItem)">{{ cengItem.name }}层</div>
                          <template>
                            <div class="ceng-wrapper">
                              <!-- Tip 号位 -->
                              <div v-if="cengItem.children.length" class="parent">
                                <!-- Tip 当前页面为《库存台账》：需要区分库位状态 -->
                                <!-- Tip 当前页面为《油墨标签》：不需要区分库位状态 -->
                                <div v-for="(kuweiItem, l) in cengItem.children" :key="l" class="child" :style="{backgroundColor: !kuweiItem.empty && from === 'inventory' ? '#FFF5A7' : '#A4E49C'}">
                                  <!-- Tip 物料信息浮窗(存在物料的号位) -->
                                  <template v-if="!kuweiItem.empty">
                                    <!-- Tip 当前页面为《库存台账》：需要提示物料信息 -->
                                    <el-popover
                                      v-if="from === 'inventory'"
                                      :key="`popover-${index}-${i}-${f}-${l}`"
                                      :ref="`popover-${index}-${i}-${f}-${l}`"
                                      :close-delay="0"
                                      :open-delay="300"
                                      placement="top"
                                      width="200"
                                      trigger="hover"
                                      popper-class="user-el-popover"
                                      :visible-arrow="false"
                                      @show="getMaterialsInformation(kuweiItem, `popover-${index}-${i}-${f}-${l}`)"
                                    >
                                      <!-- Tip 物料信息展示 -->
                                      <el-descriptions :column="1" class="user-el-descriptions">
                                        <el-descriptions-item label="物料编号">
                                          {{ materialInfo.materialCode || '无' }}
                                        </el-descriptions-item>
                                        <el-descriptions-item label="物料名称">
                                          {{ materialInfo.materialName || '无' }}
                                        </el-descriptions-item>
                                        <el-descriptions-item label="供应商">
                                          {{ materialInfo.supplierName || '无' }}
                                        </el-descriptions-item>
                                        <el-descriptions-item label="库存总数">
                                          {{ materialInfo.total || '无' }}
                                        </el-descriptions-item>
                                        <el-descriptions-item label="当前库位数量">
                                          {{ materialInfo.num || '无' }}
                                        </el-descriptions-item>
                                      </el-descriptions>
                                      <div slot="reference" style="cursor: pointer;" @click="chooseKuwei(kuweiItem)">{{ kuweiItem.name }}位</div>
                                    </el-popover>
                                    <!-- Tip 当前页面为《油墨标签》：不需要提示物料信息 -->
                                    <div v-else style="cursor: pointer;" @click="chooseKuwei(kuweiItem)">{{ kuweiItem.name }}位</div>
                                  </template>
                                  <!-- Tip 不存在物料的号位 -->
                                  <div v-else style="cursor: pointer;" @click="chooseKuwei(kuweiItem)">
                                    {{ kuweiItem.name }}位
                                  </div>
                                </div>
                              </div>
                              <!-- Tip 物料占整层 -->
                              <div v-else class="whole-floor" />
                              <div class="divider" />
                            </div>
                          </template>
                        </div>
                      </template>
                    </template>
                    <!-- Tip 物料占整个货架 -->
                    <template v-else>
                      <div class="entire-shelf" />
                    </template>
                  </el-card>
                </div>
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-scrollbar>
      <div v-if="false" class="drawer-footer">
        <el-button type="primary" @click="handleConfirm">确 定</el-button>
        <el-button @click="beforeClose">取 消</el-button>
      </div>
    </div>
  </el-drawer>
</template>

<script>
import { getWarehousePullListApi, getMaterialsInformationApi } from '@/apis/modules/base/basewmsWarehouse'
import { getChildNodeListApi, getBelowTheZoneListApi } from '@/apis/modules/base/baseWarehouseLocation'
import { requestWrapper, promiseWrapper } from '@/utils/utils'
export default {
  name: 'AddBomNodeDrawerComs',
  components: {
  },
  filters: {
    sapKuStr(name) {
      let str = ''
      name.replace(/\((.*?)\)/g, (match, content) => {
        console.log(content) // 括号内的内容
        str = content
      })
      return `(${str}库)`
    }
  },
  props: {
    value: {
      required: true,
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: '选择库位'
    },
    direction: {
      type: String,
      default: 'btt'
    },
    size: {
      type: String,
      default: '95%'
    },
    // 哪个页面的组件，库存台账的页面会展示库位物料详情
    from: {
      type: String,
      default: 'inventory'
    }
  },
  data() {
    return {
      activeName: '',
      warehousePullList: [],
      query: {
        warehouseId: ''
      },
      districtData: [], // 区
      materialInfo: {},
      pageLoading: false,
      subzoneData: [] // 区以下数据
    }
  },
  computed: {
  },
  mounted() {
    this.getWarehousePullList()
  },
  methods: {
    // 确定
    handleConfirm() {},
    // 选择区
    async chooseDistrict({ currentPosition, storageId }) {
      const diag = await promiseWrapper(this.$confirm(`是否选择（${currentPosition}）?`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false,
        distinguishCancelAndClose: true,
        closeOnPressEscape: false,
        closeOnClickModal: false,
        showClose: false // 不展示右上角关闭按钮
      }))
      // Tip 不选区(保留之前选中记录)
      if (!diag.success) {
        this.$emit('input', false)
        return
      }
      // Tip 选择区
      this.$emit('confirm', {
        currentPosition,
        storageId
      })
      this.$emit('input', false)
    },
    // 关闭
    beforeClose(done) {
      for (const item of this.districtData) {
        if (`${item.warehouseId}-${item.id}` === this.activeName) {
          this.chooseDistrict(item)
          return
        }
      }
      this.$emit('input', false)
      typeof done === 'function' && done()
    },
    // 下拉数据
    getWarehousePullList() {
      return new Promise((resolve, reject) => {
        getWarehousePullListApi().then(res => {
          const { code, data } = res
          if (!code) {
            this.warehousePullList = data || []
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 查找区以下的数据
    async getBelowTheZoneList(warehouseId, id) {
      const res = await requestWrapper(getBelowTheZoneListApi({
        warehouseId, // 仓库id
        parentId: id // 父id
      }))
      if (!res.success) return
      this.subzoneData = res.data
    },
    // 查找仓库下的区
    async selectWarehouse(val) {
      const res = await requestWrapper(getChildNodeListApi({
        warehouseId: val, // 仓库id
        parentId: null // 父id
      }))
      if (!res.success) return
      this.districtData = res.data
      this.activeName = !this.districtData.length ? '' : `${this.districtData[0].warehouseId}-${this.districtData[0].id}`
      this.districtData.length && this.getBelowTheZoneList(this.districtData[0].warehouseId, this.districtData[0].id)
    },
    // 获取物料信息
    async getMaterialsInformation(material, ref) {
      for (const key in this.$refs) {
        // 关闭当前除自个外的所有弹窗
        if (key.indexOf('popover-') !== -1) {
          if (key !== ref) {
            this.$refs[key][0] && this.$refs[key][0].doClose()
          }
        }
      }
      const { storageId } = material
      const res = await requestWrapper(getMaterialsInformationApi({ storageId }))
      if (!res.success) return
      const { data } = res
      const materialCode = [] // 物料编号集合
      const materialName = [] // 物料名称集合
      const supplierName = [] // 供应商集合
      const total = [] // 库存总数集合
      const num = [] // 当前库位数量集合
      data.forEach(item => {
        materialCode.push(item.materialCode)
        materialName.push(item.materialName)
        supplierName.push(item.supplierName)
        total.push(item.total)
        num.push(item.num)
      })
      this.materialInfo = {
        materialCode: materialCode.join(), // 物料编号
        materialName: materialName.join(), // 物料名称
        supplierName: supplierName.join(), // 供应商
        total: total.join(), // 库存总数
        num: num.join() // 当前库位数量
      }
    },
    // Tip 选择区
    handleTabClick(tab, event) {
      this.getBelowTheZoneList(this.activeName.split('-')[0], this.activeName.split('-')[1])
    },
    // Tip 选择货架
    chooseHuoJia(item) {
      // Tip 只有当前页面是《油墨标签》，才会添加这层判断
      if ((!item.labelId || item.labelId <= 0) && this.from === 'label') {
        this.$notify.warning({
          title: '警告',
          message: '当前库位没有油墨标签！'
        })
        return
      }
      this.chooseLocation(item)
    },
    // Tip 选择层
    chooseCeng(item) {
      // Tip 只有当前页面是《油墨标签》，才会添加这层判断
      if ((!item.labelId || item.labelId <= 0) && this.from === 'label') {
        this.$notify.warning({
          title: '警告',
          message: '当前库位没有油墨标签！'
        })
        return
      }
      this.chooseLocation(item)
    },
    // Tip 选择库位
    chooseKuwei(item) {
      // Tip 只有当前页面是《油墨标签》，才会添加这层判断
      if ((!item.labelId || item.labelId <= 0) && this.from === 'label') {
        this.$notify.warning({
          title: '警告',
          message: '当前库位没有油墨标签！'
        })
        return
      }
      this.chooseLocation(item)
    },
    async chooseLocation({ currentPosition, storageId }) {
      const diag = await promiseWrapper(this.$confirm(`是否选择（${currentPosition}）?`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }))
      if (!diag.success) return
      this.$emit('confirm', {
        currentPosition,
        storageId
      })
      this.$emit('input', false)
    },
    getHjWidth(arr = []) {
      if (arr && arr.length) { // 当前货架不为空
        const is600 = arr.some(item => {
          return item.children && item.children.length > 5 && item.children.length <= 10
        })
        const is900 = arr.some(item => {
          return item.children && item.children.length > 10 && item.children.length <= 15
        })
        const is1200 = arr.some(item => {
          return item.children && item.children.length > 15 && item.children.length <= 20
        })
        if (is600) return '660px'
        if (is900) return '960px'
        if (is1200) return '1260px'
        return '360px'
      }
      return '360px'
    }
  }
}
</script>

<style lang='scss' scoped>
::v-deep:focus {
    outline: 0;
}
.draw-container{
    padding: 0 20px;
}
.scroll-drawer {
    height: calc(100vh - 220px);
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
}
::v-deep .user-drawer.el-drawer {
    width: 100%;
}
// 货架样式
.huojia-sty {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    & > div {
        margin: 0 30px 15px;
    }
}
// 层
.tier {
    display: flex;
    align-items: center;
    margin: 5px 0 10px;
    width: 100%;
    // 层名称展示样式
    .floor-name-sty {
        width: 50px;
        height: 50px;
        margin: 0 5px;
        text-align: center;
        line-height: 50px;
        background-color: #a3a2a2;
        cursor: pointer;
    }
    // 层内容展示样式
    .ceng-wrapper {
        position: relative;
        width: calc(100% - 60px);
        // 层内容内部展示样式
        .parent {
            display: flex;
            align-items: center;
            .child {
                width: 50px;
                height: 50px;
                text-align: center;
                line-height: 50px;
                background: #a3a2a2;
                margin: 0 5px;
                border-radius: 10px;
                font-size: 13px;
            }
        }
        // 整层样式
        .whole-floor {
            width: 100%;
            height: 50px;
            background-color: #A4E49C;
        }
        // 层下边分隔线
        .divider {
            width: 100%;
            height: 1px;
            background-color: #BEBEBE;
            box-shadow: 2px 2px 2px #BEBEBE;
            position: absolute;
            left: 0;
            bottom: -3px;
            margin: 0;
        }
    }
}
.entire-shelf {
    width: 100%;
    min-height: 325px;
    // height: 100%;
    background-color: #A4E49C;
}
</style>

<style lang="scss">
.el-popover.user-el-popover {
    border-radius: 10px;
    background-color: #E0F4FF;
    opacity: .9;
    .user-el-descriptions {
        &.el-descriptions {
            .el-descriptions__body {
                background-color: #E0F4FF !important;
                opacity: .9;
            }
        }
    }
}
.drawer-footer{
  text-align: right;
}
</style>

