<template>
  <div class="app-container">
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
        <el-row>
          <el-form-item label="按位置查询 : " prop="storageId">
            <el-link
              type="primary"
              style="min-width: 178px"
              @click="chooseLocation"
            >
              {{ query.storageId ? query.currentPosition : "请选择位置" }}
            </el-link>
          </el-form-item>
          <el-button v-if="query.storageId" type="danger" @click="clearLocation">清除</el-button>
        </el-row>
        <el-row>
          <el-form-item prop="materialCodeOrName" label="按物料查询 : ">
            <el-input
              v-model.trim="query.materialCodeOrName"
              clearable
              placeholder="物料编号或名称"
              class="filter-item"
              style="width: 178px;"
              @keyup.enter.native="searchInit"
            />
          </el-form-item>
          <el-form-item prop="materialGroupCode">
            <el-select
              v-model="query.materialGroupCode"
              filterable
              remote
              placeholder="物料组编号"
              :remote-method="remoteMethod"
              :loading="selectLoading"
              class="filter-item"
              style="width: 178px;"
              clearable
              @clear="clearInfo"
            >
              <el-option
                v-for="item in options"
                :key="item.code"
                :label="item.code"
                :value="item.code"
              />
            </el-select>
          </el-form-item>
          <el-form-item prop="materialType">
            <el-select
              v-model="query.materialType"
              class="filter-item"
              placeholder="物料类型"
              filterable
              clearable
              style="width: 178px;"
            >
              <el-option v-for="(item, index) in dict['wms_material_type']" :key="index" :label="item.label" :value="item.value" />
            </el-select>
          </el-form-item>
          <el-form-item prop="assessType">
            <el-select
              v-model="query.assessType"
              class="filter-item"
              placeholder="评估类型"
              filterable
              clearable
              style="width: 178px;"
            >
              <el-option v-for="(item, index) in dict['wms_material_assess_type']" :key="index" :label="item.label" :value="item.value" />
            </el-select>
          </el-form-item>
          <el-form-item prop="batchNoOrBarcode">
            <el-input
              v-model.trim="query.batchNoOrBarcode"
              clearable
              placeholder="批次号或SN号"
              class="filter-item"
              style="width: 178px;"
            />
          </el-form-item>
          <!-- <el-form-item prop="expirationDate">
            <el-input-number
              v-model="query.expirationDate"
              :min="0"
              placeholder="保质期预警天数"
              style="width: 178px;"
            />
          </el-form-item> -->
          <el-form-item prop="materialImportantGrade">
            <el-select
              v-model="query.materialImportantGrade"
              class="filter-item"
              placeholder="重要等级"
              filterable
              clearable
              style="width: 178px;"
            >
              <el-option v-for="(item, index) in dict['wms_material_important_grade']" :key="index" :label="item.label" :value="item.value" />
            </el-select>
          </el-form-item>
          <el-form-item prop="earlyWarningType">
            <el-select
              v-model="query.earlyWarningType"
              class="filter-item"
              placeholder="预警类型"
              filterable
              clearable
              style="width: 178px;"
            >
              <el-option label="安全库存上限" :value="1" />
              <el-option label="安全库存下限" :value="2" />
              <el-option label="超期预警" :value="3" />
            </el-select>
          </el-form-item>
        </el-row>
        <!-- <el-row>
          <el-form-item label="保质期预警: " prop="startTime">
            <el-input-number v-model="query.num" :min="1" :max="10" label="描述文字" />
          </el-form-item>
        </el-row> -->
        <el-row>
          <el-form-item label="按时间查询 : " prop="startTime">
            <el-date-picker
              v-model="query.startTime"
              class="filter-item"
              type="date"
              style="width: 178px;"
              value-format="yyyy-MM-dd"
              placeholder="开始时间"
              @change="query.endTime = ''"
            />
          </el-form-item>
          <el-form-item prop="endTime">
            <el-date-picker
              v-model="query.endTime"
              class="filter-item"
              type="date"
              style="width: 178px;"
              value-format="yyyy-MM-dd"
              placeholder="结束时间"
              :picker-options="{disabledDate: (time) => time.getTime() < new Date(query.startTime).getTime()}"
            />
          </el-form-item>
          <el-button
            class="filter-item"
            type="success"
            icon="el-icon-search"
            @click="searchInit"
          >搜索</el-button>
          <el-button
            class="filter-item"
            type="warning"
            icon="el-icon-refresh-left"
            @click="resetForm('filterItem')"
          >重置</el-button>
        </el-row>
      </el-form>
    </div>
    <el-descriptions :column="2">
      <el-descriptions-item label="台账总金额">{{ totalAmount || '---' }}</el-descriptions-item>
    </el-descriptions>
    <el-row style="text-align: right;">
      <el-button type="success" icon="el-icon-download" @click="downloadTemplate">下载模板</el-button>
      <template v-if="!sortInfo">
        <el-button type="primary" @click="groupingStatistics">分组统计</el-button>
        <el-button type="primary" icon="el-icon-download" @click="exportAccount">台账导出</el-button>
      </template>
      <template v-else>
        <el-button type="primary" @click="cancelGroupingStatistics">取消分组统计</el-button>
        <el-button type="primary" icon="el-icon-download" @click="exportList">分组统计导出</el-button>
      </template>
      <!-- 仓库管理/库存台账 导入 -->
      <form-upload-file
        ref="uploadCom"
        accept=".xls,.xlsx"
        style="margin-left: 10px;"
        url="/warehouseMgt/wmsInventoryBookDetail/import"
        btn-content="台账导入"
        btn-icon="el-icon-upload"
        :limit="99999"
        :show-file-list="false"
        @uploadFileSuccess="handleUploadSuccess"
        @uploadFileError="handleUploadFileError"
      >
        <template v-slot:tip />
      </form-upload-file>
    </el-row>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :cell-style="handleCellStyle"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="120"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        @refresh="refresh"
      >
        <template slot-scope="scope">
          <el-button v-if="!scope.row.isSubtotalsRow && !scope.row.isTotalsRow" type="warning" @click="toDetail(scope.row)">详情</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination v-if="isPage" :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    <storage-location v-if="kuweiVisible" v-model="kuweiVisible" @confirm="selectedStorageLocation" />
    <sort v-model="visible" :extra-info="{columnList}" @confirm="handleConfirm" />
    <order-info v-model="visible1" :extra-info="rowInfo" />
    <reservation-info v-model="visible2" :extra-info="rowInfo" />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import paginationPage from '@/mixins/paginationPage'
import { getListApi, groupExportApi, groupListApi, accountExportApi } from '@/apis/modules/warehouse/inventory'
import { getPullListItemApi } from '@/apis/modules/base/baseItemGroup'
import StorageLocation from './StorageLocation'
import Sort from './Sort'
import OrderInfo from './OrderInfo'
import ReservationInfo from './ReservationInfo'
import { requestWrapper } from '@/utils/utils'
import tableHeadSet from '@/mixins/tableHeadSet'
import { formUploadFilesMixins } from '@/mixins/upload'

export default {
  name: 'WarehouseInventory',
  components: {
    StorageLocation,
    Sort,
    OrderInfo,
    ReservationInfo
  },
  mixins: [paginationPage, tableHeadSet, formUploadFilesMixins],
  props: {
    // 路由params参数
    materialCode: {
      type: [String, Number],
      default: ''
    }
  },
  dicts: ['wms_material_assess_type', 'wms_material_type', 'wms_material_important_grade'],
  data() {
    return {
      title: this.$route.meta.title,
      loading: false,
      query: {
        storageId: '', // 库位id
        currentPosition: '', // 库位名称
        materialGroupCode: '', // 物料分组
        materialType: '', // 物料类型
        assessType: '', // 评估类型
        materialCodeOrName: '', // 物料编码或名称
        batchNoOrBarcode: '', // 批次号或SN号
        num: '',
        // expirationDate: '', // 保质期预警天数
        earlyWarningType: '', // 预警类型
        materialImportantGrade: '', // 重要等级
        startTime: '', // 开始时间
        endTime: '' // 结束时间
      },
      tableData: [],
      totalAmount: '', // 台账总金额(列表左上方展示)
      columnSets: [
        {
          prop: 'outWarehouse',
          children: [
            {
              module: 'word',
              prop: 'outWarehouseNum',
              label: '数量',
              align: 'center',
              minWidth: '',
              fixed: false,
              sortable: false,
              resizable: true,
              formatter: null,
              headerAlign: 'center'
            },
            {
              module: 'word',
              prop: 'outWarehouseAmount',
              label: '金额',
              align: 'center',
              minWidth: '',
              fixed: false,
              sortable: false,
              resizable: true,
              formatter: null,
              headerAlign: 'center'
            }
          ]
        },
        {
          prop: 'inWarehouse',
          children: [
            {
              module: 'word',
              prop: 'outWarehouseNum',
              label: '数量',
              align: 'center',
              minWidth: '',
              fixed: false,
              sortable: false,
              resizable: true,
              formatter: null,
              headerAlign: 'center'
            },
            {
              module: 'word',
              prop: 'outWarehouseAmount',
              label: '金额',
              align: 'center',
              minWidth: '',
              fixed: false,
              sortable: false,
              resizable: true,
              formatter: null,
              headerAlign: 'center'
            }
          ]
        },
        {
          module: 'word',
          prop: 'earlyWarningInfo',
          align: 'center',
          minWidth: '200',
          fixed: false,
          sortable: false,
          resizable: true,
          vHtml: true,
          formatter: item => `${item.earlyWarningInfo.split(',').map(a => `${a}<br />`).join('')}`,
          headerAlign: 'center'
        },
        // 未到货数量
        {
          module: 'link',
          prop: 'inTransitNum',
          formatter: (row) => row.inTransitNum,
          handleClick: (c) => {
            console.log('未到货数量', c)
            if (!c.inTransitNum) return
            this.visible1 = true
            this.rowInfo = { ...c }
          }
        },
        // 预留数量
        {
          module: 'link',
          prop: 'billReservedNum',
          formatter: (row) => row.billReservedNum,
          handleClick: (c) => {
            console.log('预留数量', c)
            if (!c.billReservedNum) return
            this.visible2 = true
            this.rowInfo = { ...c }
          }
        }
      ],
      options: [],
      selectLoading: false,
      kuweiVisible: false,
      isPage: true, // 是否展示分页组件
      visible: false,
      sortInfo: null,
      visible1: false,
      visible2: false,
      rowInfo: {}
    }
  },
  computed: {
    ...mapGetters('app', {
      navbarWarehouseCheckedList: 'navbarWarehouseCheckedList'
    })
  },
  watch: {
    navbarWarehouseCheckedList: {
      handler() {
        this.init()
      },
      deep: true
    }
  },
  mounted() {
    this.materialCode && (this.query.materialCodeOrName = this.materialCode)
    this.init()
  },
  methods: {
    init(type, sortParams = {}) {
      this.loading = true
      type === 'sort' ? this.isPage = false : this.isPage = true
      const params = {
        ...type !== 'sort' && { page: {
          ...this.pageInfo
        }},
        ...type === 'sort' && sortParams,
        ...this.query,
        warehouseIds: this.navbarWarehouseCheckedList.map(item => item.id)
      }
      const api = type === 'sort' ? groupListApi : getListApi
      return new Promise((resolve, reject) => {
        api({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData, otherData, pageList } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            this.totalAmount = otherData.totalAmount
            if (type !== 'sort') {
              const { list, ...pageInfo } = pageData
              this.initPage(pageInfo)
              this.tableData = list
            } else {
              this.tableData = pageList
            }
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.query.currentPosition = ''
      this.searchInit()
    },
    toDetail({ materialId }) {
      this.$router.push({
        name: 'WarehouseInventoryDetail',
        params: {
          materialId
        }
      })
    },
    chooseLocation() {
      // this.$router.push({
      //   name: 'WarehouseChooseWarehouseLocation'
      // })
      this.kuweiVisible = true
    },
    clearLocation() {
      this.query.currentPosition = ''
      this.query.storageId = ''
    },
    selectedStorageLocation({ currentPosition, storageId }) {
      this.query.currentPosition = currentPosition
      this.query.storageId = storageId
    },
    // 下拉数据
    // 下拉框输入值发生变化时调用
    async remoteMethod(query) {
      this.options = []
      if (query !== '') {
        this.selectLoading = true
        // Tip 远程下拉选项
        const res = await requestWrapper(getPullListItemApi({
          materialGroupCode: query
        }))
        if (!res.success) return
        this.selectLoading = false
        this.options = res.data
      } else {
        this.options = []
      }
    },
    clearInfo() {
      // console.log()
      this.options = []
    },
    // Tip 当前行是否有预警信息，高亮展示
    handleCellStyle({ row, rowIndex }) {
      let cellStyle = {}
      // 非统计行
      if (row.earlyWarningInfo) {
        cellStyle = {
          backgroundColor: '#F56C6C'
        }
      } else {
        // isSubtotalsRow 是否小计行
        // isTotalsRow 是否总计行
        row.isSubtotalsRow && (cellStyle = {
          backgroundColor: '#FDFEDF',
          fontWeight: 750
        })
        row.isTotalsRow && (cellStyle = {
          backgroundColor: '#FEFDBF',
          fontWeight: 900
        })
      }
      return cellStyle
    },
    // 分组统计弹窗确认
    handleConfirm(params) {
      this.sortInfo = params
      this.init('sort', params)
    },
    // 分组统计
    groupingStatistics() {
      this.visible = true
    },
    // 取消分组统计
    cancelGroupingStatistics() {
      this.sortInfo = null
      this.init()
    },
    // 台账导出
    exportAccount() {
      // this.$notify.warning({
      //   title: '警告',
      //   message: '功能维护中'
      // })
      const params = {
        ...this.query,
        warehouseIds: this.navbarWarehouseCheckedList.map(item => item.id)
      }
      this.$confirm('是否导出库存台账?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      })
        .then(() => {
          accountExportApi(params)
            .then(res => {
              this.download(res, '库存台账.xlsx')
            })
            .catch(e => {
              console.log(e)
              this.$notify.error({
                title: '错误',
                message: '导出错误，请重试'
              })
            })
        })
        .catch(err => {
          console.log('err', err)
        })
    },
    // Tip 分组统计导出
    exportList() {
      const params = {
        ...this.sortInfo,
        ...this.query,
        warehouseIds: this.navbarWarehouseCheckedList.map(item => item.id)
      }
      this.$confirm('是否导出库存台账分组统计?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      })
        .then(() => {
          groupExportApi(params)
            .then(res => {
              this.download(res, '库存台账分组统计.xlsx')
            })
            .catch(e => {
              console.log(e)
              this.$notify.error({
                title: '错误',
                message: '导出错误，请重试'
              })
            })
        })
        .catch(err => {
          console.log('err', err)
        })
    },
    download(result, name) {
      const filename = name
      const blob = new Blob([result.data], { type: 'application/vnd.ms-excel' })
      const url = window.URL.createObjectURL(blob)
      if (window.navigator.msSaveBlob) { // IE
        try {
          window.navigator.msSaveBlob(blob, filename)
        } catch (e) {
          console.log(e)
        }
      } else { // 非IE
        const link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.setAttribute('download', filename)
        document.body.appendChild(link)
        link.click()
      }
      URL.revokeObjectURL(url) // 释放内存
    },
    refresh() {
      this.sortInfo ? this.init('sort', this.sortInfo) : this.searchInit()
    },
    // 附件上传成功
    handleUploadSuccess() {
      this.init()
      this.$notify.success({
        title: '成功',
        message: '上传成功'
      })
    },
    // 附件上传失败
    handleUploadFileError() {
      this.$notify.error({
        title: '错误',
        message: '上传失败'
      })
    },
    // 下载模板
    downloadTemplate() {
      window.open(`${process.env.VUE_APP_BASE_API_WMS}/warehouseMgt/wmsInventoryBookDetail/downloadBookImportTemplate`)
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .el-form .el-row{
  margin-bottom: 10px;
}
</style>
