<template>
  <el-dialog
    title="分组统计"
    top="15vh"
    :visible="value"
    :show-close="true"
    width="35%"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="Form" :model="form" :rules="gsRules" label-width="120px" label-position="top">
      <el-form-item label="表头可排序项：" prop="groupFieldName">
        <el-radio-group v-model="form.groupFieldName" class="my-radio-group">
          <el-radio v-for="(item, index) in extraInfo.columnList" :key="index" :label="item.prop" border>{{ item.label }}</el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <span slot="footer">
      <el-button type="primary" @click="handleSave('Form')">确 定</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </span>
  </el-dialog>
</template>

<script>
const INIT_FORM = () => ({
  groupFieldName: ''
})
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      visible: false,
      form: INIT_FORM(),
      gsRules: {
        groupFieldName: [
          { required: true, message: '请选择可排序项', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    // 表单提交
    handleSave(formName) {
      Promise.all([
        this.$refs[formName].validate()
          .then(res => {
            console.log('res', res)
            this.$emit('confirm', { ...this.form, calculateFieldNames: ['inventoryNum', 'totalAmount'] })
            this.beforeClose()
          })
          .catch(err => {
            console.log('err', err)
            return Promise.reject(err)
          })
      ])
    },
    // 弹窗关闭前操作
    beforeClose(done) {
      this.$refs['Form'].resetFields()
      this.form = INIT_FORM()
      typeof done === 'function' && done()
      this.$emit('input', false)
    },
    // 弹窗打开初始化
    initSelect() {
    }
  }
}
</script>

<style scoped lang="scss">
::v-deep .my-radio-group {
  .el-radio {
    margin: 0 15px 5px 0;
    &.is-bordered {
      margin-left: 0;
    }
  }
  display: flex;
  flex-flow: row wrap;
}
</style>
